body {
  background: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #333;
}

form {
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

input[type="text"] {
  width: 300px;
  margin-right: 10px;
  padding: 10px;
}

.select-category {
  width: 150px;
  margin-right: 10px;
  padding: 10px;
}

.button {
  color: #fff;
  cursor: pointer;
  background: #28a745;
  border: none;
  padding: 10px 15px;
}

.button:hover {
  background: #218838;
}

.columns {
  justify-content: space-between;
  display: flex;
}

.column {
  background: #fff;
  width: 30%;
  padding: 10px;
  box-shadow: 0 0 10px #0000001a;
}

.task {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  transition: all .3s;
}

.task p {
  margin: 5px 0;
}

.task button {
  margin-top: 10px;
}

.task input[type="text"] {
  width: 200px;
  margin-right: 10px;
  padding: 5px;
}

.task:hover {
  box-shadow: 0 0 10px #888;
}

.error {
  color: red;
  margin-bottom: 10px;
  font-weight: bold;
}

.add-task-form {
  flex-wrap: wrap;
  margin-bottom: 20px;
  display: flex;
}

.add-task-form form {
  width: calc(100% - 120px);
  margin-bottom: 10px;
  margin-right: 20px;
}

.category-indicators {
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  display: flex;
}

.category-indicator {
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 30px;
  margin-bottom: 10px;
  margin-right: 10px;
  display: flex;
}

.ux {
  background-color: #ffecb3;
}

.dev-frontend {
  background-color: #c8e6c9;
}

.dev-backend {
  background-color: #b3e5fc;
}

.category-indicator span {
  color: #000;
  font-size: 14px;
  font-weight: bold;
}

@media screen and (width <= 768px) {
  .columns {
    flex-direction: column;
  }

  .column {
    width: 100%;
    margin-bottom: 20px;
  }

  .task input[type="text"] {
    width: 150px;
  }

  .app h1 {
    font-size: 24px;
  }

  .add-task-form form {
    width: 100%;
    margin-right: 0;
  }

  input[type="text"], .select-category, .button {
    width: calc(100% - 20px);
    margin-bottom: 10px;
    margin-right: 0;
  }
}

.task.ux {
  background-color: #ffecb3;
}

.task.dev-frontend {
  background-color: #c8e6c9;
}

.task.dev-backend {
  background-color: #b3e5fc;
}
